}
/********************************************************顯示名稱**************************************************************/
.name[data-v-5ec51c36]{
    margin: 20px;
    position: relative;
}
/********************************************************更改名稱部分***********************************************************/
.userName[data-v-5ec51c36]{
    margin: 0;
    height: 30px;
    font-size: 16px;
    margin-bottom: 10px;
    border: solid 1px #cbcbcb;
    width: 70%;
    text-align: center;
    display: inline-block;
}
.reset[data-v-5ec51c36]{
    margin-top: 10px;
    padding: 5px 10px;
    border-radius: 10px;
    font-weight: 600;
    color: #333333;
    background-color: #CCCCCC;
    display: inline-block;
    cursor: pointer;
}
.button[data-v-5ec51c36]{
    font-size: 14px;
    color: white;
    background-color: #2B364B;
    padding: 10px 20px;
    display: inline-block;
    cursor: pointer;
}
.modal[data-v-5ec51c36]{
    background-color: rgba(0,0,0,0.4);
    width: 100%;
    height: 100%;
    text-align: center;
    position: fixed;
    z-index: 1;
    left: 0%;
    top: 0%;
    -webkit-animation-name: animatetop-data-v-5ec51c36;
            animation-name: animatetop-data-v-5ec51c36;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}
.modal_container[data-v-5ec51c36]{
    position: relative;
    padding: 30px;
    max-width: 300px;
    outline: 0;
}
.modal_header[data-v-5ec51c36]{
    color: white;
    background-color: #2B364B;
    padding: 10px;
    text-align: center;
    border-radius: 5px 5px 0px 0px;
}
.modal_body[data-v-5ec51c36]{
    background-color: white;
    padding: 20px 50px;
    text-align: center;
}
@-webkit-keyframes animatetop-data-v-5ec51c36 {
from {top:-300px; opacity:0
}
to {top:20; opacity:0.8
}
}
@keyframes animatetop-data-v-5ec51c36 {
from {top:-300px; opacity:0
}
to {top:20; opacity:0.8
}
}
/**********************************************************************聊天室*****************************************************/
.chatRoom[data-v-5ec51c36]{
    border-radius: 5px;
    max-width: 500px;
    -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
            box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}
.chatHeader[data-v-5ec51c36]{
    width: 100%;
    height: 85px;
    color: white;
    background-color: #2B364B;
    border-radius: 5px 5px 0px 0px;
    position: relative;
}
.room-image[data-v-5ec51c36]{
    margin: 15px;
    border-radius: 50%;
    cursor: pointer;
}
.room-title[data-v-5ec51c36]{
    font-size: 16px;
    font-weight: bolder;
    margin: 30px 0px 0px 5px;
}
/*顯示聊天內容部分*/
.chatBody[data-v-5ec51c36]{
    padding: 10px 0px;
    background-color: #fff;
    height: 55vh;
    overflow-y: auto;
    overflow-x: hidden;
}
.message-box[data-v-5ec51c36]{
  padding: 10px;
  position: relative;
  text-align: left;
}
.message-box img[data-v-5ec51c36]{
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
}
.message-content[data-v-5ec51c36]{
  display: inline-block;
}
.message-name[data-v-5ec51c36]{
  text-align: left;
}
.message-text[data-v-5ec51c36]{
  display: inline-block;
  font-size: 12px;
  color: #35393D;
  letter-spacing: 0.6px;
  background-color: #E3E8EB;
  border-radius: 12px;
  line-height: 1.5;
  text-align: left;
  word-break: break-all;
  white-space: pre-line;
}
.message-message[data-v-5ec51c36]{
  padding: 8px 10px 9px 11px;
  max-width: 200px;
  overflow: hidden;
}
.message-time[data-v-5ec51c36]{
  display: inline-block;
  font-size: 12px;
}
/*使用者顯示內容部分*/
.message-user[data-v-5ec51c36]{
  position: relative;
  text-align: right;
}
/*上傳檔案*/
.loadDataArea[data-v-5ec51c36]{
    border-top: solid 1px #E7E7E7;
    border-bottom: solid 2px #E7E7E7;
    background-color: #F6F6F6;
    height: 35px;
    padding: 0px 5px;
    position: relative;
}
.loadData[data-v-5ec51c36]{
    padding: 5px;
    display: inline-block;
    overflow: hidden;
}
.loadData[data-v-5ec51c36]:hover{
    border: solid 1px #DCDCDC;
}
.loadData input[data-v-5ec51c36]{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    cursor: pointer;
    /* 讓input file可以支援pointer要加pl100% */
    padding-left: 100%;
}
.loadData img[data-v-5ec51c36]{
    width: 20px;
    height: 20px;
}
/*聊天打字區域*/
.ChatArea[data-v-5ec51c36]{
    padding: 5px;
    height: 8vh;
}
.ChatArea-message[data-v-5ec51c36]{
    width: 100%;
    border: none;
    resize: none;
    outline: none;
}
